/* General Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .admin-homepage {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  /* Header Styles */
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0078d7;
    color: white;
    padding: 10px 20px;
  }
  
  .logo {
    display: flex;
    align-items: center;
  }
  
  .logo img {
    height: 40px;
    margin-right: 10px;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
    font-weight: bold;
  }
  
  .admin-navbar a:hover {
    text-decoration: underline;
  }
  
  /* Main Content Styles */
  .admin-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f0f0f0;
    text-align: center;
  }
  
  .admin-main h1 {
    font-size: 24px;
    background-color: #ffffff;
    padding: 10px 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    
  }
  
  .admin-image img {
    max-width: 80%;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    
  }
  
  /* Footer Styles */
  .admin-footer {
    background-color: black;
    color: white;
    text-align: center;
    padding: 10px 0;
  }
  